          company: 'Extra-Web SRL',
          location: 'Parma, (Italy)',
          description: 'I implemented highly customised and fully responsive websites using Wordpress as a CMS but not only: I also created ad-hoc implementations of those websited using the specific application of HTML5, CSS3 and JavaScript to obtain original and dynamic results.',
